#Servicios {
  padding-top: 90px !important;
  height: 100%;
  width: 100%;
  font-family: 'Berkshire Swash', sans-serif;
  text-align: center;
}

#title-services {
  font-size: 4rem;
  padding-bottom: 0rem !important;
  padding-top: 0rem !important;
  font-family: "Berkshire Swash", sans-serif;
  font-weight: 500;
  line-height: 1.2;
  color: white;
}

.img-container{
  height: 60%;
  display: flex;
  width: 100%;

}

.card-container {
  width: 95%;
  height: 70vh;
  margin: auto;
  display: flex;
  justify-content: space-around;
}

.card {
  position: relative;
width: 23%;
height: 95%;
margin: 0 10px;
border-radius: 30px;
perspective: 1000px;
cursor: pointer;
margin: auto;
}

.card .front,
.card .back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  transition: transform 0.6s ease-in-out;
} 

.card .front {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: none;
  border: 1px solid #ddd;
  border-radius: 10%;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.5);
  background: linear-gradient(#3f3045, black);

}

.card .front img {
  border-radius: 100%;
margin: auto;
max-height: 190px;
max-width: 190px;
height: 100%;
width: 100%;
border: 2px solid #ddd;
box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.5);


}

.card .front h2 {
  width: 90%;
  padding-bottom: 0rem;
  padding-top: 0rem;
  font-size: 1.8em;
  margin: auto;
  color: white;
}

.card .back {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: none;
  border: 1px solid #ddd;
  border-radius: 10%;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.5);
  background: linear-gradient(#3f3045, black);
  display: flex;
  justify-content: center;
  align-items: center;
  transform: rotateY(-180deg);
}

.card.flipped .front {
  transform: rotateY(180deg);
}

.card.flipped .back {
  transform: rotateY(0);
}

.card .back p {
  font-family: 'Cinzel Decorative';
font-size: 13px;
margin: 0;
padding: 20px;
text-align: justify;
color: white;
}



#text-container{
  background: linear-gradient(rgba(255, 33, 79, 0.95), rgba(0, 0, 0, 0.95));
  color: white;
  opacity: 100;
  font-size: 150%;
  display: inline-flex;
  justify-content: space-around;
  border: 2px solid #ff214f;
  padding: 0.2rem;
  line-height: 2;
  border-radius: 2rem;
  align-items: center;
  width: 45%;
}

#text-container h2 {
  padding: 1em 0.5em 1em 0.5em;
  overflow: hidden;
  font-size: 1em;
  font-family: Proxima Nova;
}


@media (min-width: 900px) and (max-width: 1200px) {

  .card-container {
    width: 90%;
    height: 150vh;
    margin: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: grid;
    justify-content: space-around;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
  }

  .card {
    width: 90%;
  }

  #text-container{
    width: 60%;
  }
}

@media (max-width: 900px) {
  .card-container {
    flex-direction: column;
    display: flex;
    height: 280vh;
    align-items: center;
  }

  .card {
    margin: 10px 0;
    width: 90%;
  }

  #text-container{
  width: 80%;
  margin: 5% 0;
  }


}
